<template>
  <div class="wechat-callback">...</div>
</template>

<script>
import authService from '@/utils/auth'
import { useToast } from 'vue-toastification'

export default {
  name: 'WeChatCallback',
  setup() {
    const toast = useToast()
    return { toast }
  },
  async created() {
    const code = this.$route.query.code
    if (!code) {
      this.toast.error('')
      this.$router.push('/auth')
      return
    }

    const { success, user, error } = await authService.loginWithWeChat(code)
    if (success) {
      this.toast.success(`${user.contact || ''}`)
      this.$router.push('/')
    } else {
      this.toast.error(error)
      this.$router.push('/auth')
    }
  }
}
</script>

<style scoped>
.wechat-callback {
  padding: 20px;
  text-align: center;
}
</style>

